/* General Layout */
.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
  }
  
  /* Header */
  h2 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  /* Project Grid */
  .project-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  /* Project Card Styling */
  .project-card {
    background: white;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    text-decoration: none;
    color: black;
    display: block;
    text-align: center;
  }
  
  .project-card img {
    width: 100%;
    height: 180px;
    object-fit: cover;
    border-radius: 8px;
  }
  
  .project-card p {
    font-size: 1rem;
    font-weight: bold;
    margin-top: 10px;
  }
  
  /* Hover Effects */
  .project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
    background: #f0f0f0;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .project-grid {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }
  }
  